.reg {
  padding: 50px 0;
}
.reg__header {
  position: relative;
  display: flex;
  gap: 32px;
  align-items: center;
  padding-bottom: 42px;
  margin-bottom: 53px;
}
.reg__header::before {
  position: absolute;
  z-index: 1;
  content: "";
  bottom: 1px;
  width: 60%;
  height: 1px;
  background-color: #D8B075;
}
.reg__header::after {
  position: absolute;
  z-index: 2;
  content: "";
  bottom: 0;
  width: 30%;
  height: 3px;
  background-color: #AA804F;
}
.reg__header-text {
  display: inline-block;
  width: 50%;
  margin-top: -18px;
  font-family: Optima;
  font-size: 42px;
  font-weight: 400;
  line-height: 54.6px;
  letter-spacing: -0.01em;
  text-align: left;
  color: #181C1D;
  text-transform: uppercase;
}
@media (max-width: 900px) {
  .reg__header {
    flex-direction: column;
  }
  .reg__header::before {
    width: 100%;
  }
  .reg__header::after {
    left: 0;
    width: 50%;
  }
  .reg__header-value {
    font-size: 150px;
    font-weight: 400;
    line-height: 190px;
  }
  .reg__header-text {
    font-size: 32px;
    line-height: 45px;
    width: 100%;
    text-align: center;
  }
}
.reg__notice {
  display: inline-block;
  margin-bottom: 32px;
  width: 33%;
  font-family: Onest;
  font-size: 18px;
  font-weight: 400;
  line-height: 22.95px;
  text-align: left;
  color: rgba(46, 50, 55, 0.6392156863);
}
@media (max-width: 1400px) {
  .reg__notice {
    width: 100%;
  }
}

.reg__form {
  display: flex;
  row-gap: 16px;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
}
.reg__form input, .reg__form button, .reg__form .form__wrapper--agreement {
  width: 32%;
  max-width: 452px;
}
@media (max-width: 1000px) {
  .reg__form input, .reg__form button, .reg__form .form__wrapper--agreement {
    width: 48%;
  }
}
@media (max-width: 700px) {
  .reg__form input, .reg__form button, .reg__form .form__wrapper--agreement {
    width: 100%;
    max-width: 100%;
  }
}
.reg__form button {
  height: 68px;
}
@media (max-width: 1000px) {
  .reg__form button {
    width: 100%;
    max-width: 100%;
    margin: 0 auto;
  }
}