.header {
  position: fixed;
  z-index: 99;
  width: 100%;
  transition: box-shadow 0.3s, height 0.3s, transform 0.3s;
  background-color: #ffffff;
}
.header__header {
  display: flex;
  align-items: center;
  height: 50px;
  width: 100%;
  padding: 0 20px;
  background-color: #2E3237;
}
@media only screen and (max-width: 1000px) {
  .header__header {
    justify-content: space-between;
  }
}
@media only screen and (max-width: 500px) {
  .header__header {
    flex-direction: column;
    height: auto;
    align-items: flex-start;
    padding: 4px 20px;
  }
}
.header__location {
  display: flex;
  flex-shrink: 0;
  gap: 12px;
  font-size: 14px;
  font-weight: 400;
  line-height: 17.85px;
  text-align: left;
}
.header__location span:first-child {
  color: rgba(243, 240, 226, 0.4196078431);
}
.header__location span:nth-child(2) {
  color: rgba(243, 240, 226, 0.8588235294);
  cursor: pointer;
}
.header__info {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  width: 45%;
  justify-content: space-between;
  margin: 0 auto;
}
.header__info-item {
  flex-shrink: 0;
}
.header__info-link {
  font-size: 14px;
  font-weight: 400;
  line-height: 17.85px;
  text-align: left;
  color: #9B9B9B;
  transition: opacity 0.3s;
}
@media (hover: hover) {
  .header__info-link:hover {
    opacity: 0.8;
  }
}
@media only screen and (max-width: 1400px) {
  .header__info-link {
    font-size: 12px;
    line-height: 15px;
  }
}
@media only screen and (max-width: 1200px) {
  .header__info {
    gap: 5px;
  }
}
@media only screen and (max-width: 1000px) {
  .header__info {
    display: none;
  }
}
.header__first-mail {
  font-size: 14px;
  font-weight: 400;
  line-height: 17.85px;
  text-align: left;
  color: #9B9B9B;
  transition: opacity 0.3s;
}
@media (hover: hover) {
  .header__first-mail:hover {
    opacity: 0.8;
  }
}
.header__wrapper {
  position: relative;
  width: 100%;
  display: flex;
  padding: 36px 0 100px 0;
  align-items: center;
  transition: padding 0.3s;
}
@media only screen and (max-width: 1000px) {
  .header__wrapper {
    padding: 10px 0;
  }
}
.header__logo {
  display: flex;
  width: 220.64px;
  height: 37px;
  flex-shrink: 0;
  margin-right: auto;
}
.header__logo img {
  position: relative;
  flex-shrink: 0;
  width: 220.64px;
  height: 37px;
}
@media only screen and (max-width: 1400px) {
  .header__logo {
    margin-right: 0;
  }
}
@media only screen and (max-width: 1000px) {
  .header__logo {
    margin: 0 auto;
    width: 177px;
    height: 30px;
  }
  .header__logo img {
    width: 177px;
    height: 30px;
  }
}
.header__contacts {
  display: flex;
  margin-right: 24px;
  justify-content: space-between;
}
@media only screen and (max-width: 1000px) {
  .header__contacts {
    margin-right: 0;
  }
}
.header__tel {
  display: flex;
  align-items: center;
  gap: 30px;
}
.header__tel-link {
  font-size: 18px;
  color: #2E3237;
  font-weight: 600;
  line-height: 22.95px;
  transition: opacity 0.3s;
}
@media (hover: hover) {
  .header__tel-link:hover {
    opacity: 0.8;
  }
}
.header__tel-list {
  display: flex;
  flex-direction: column;
}
@media only screen and (max-width: 1400px) {
  .header__tel {
    gap: 14px;
  }
  .header__tel-link {
    font-size: 14px;
    font-weight: 500;
    line-height: 18px;
  }
}
.header__nav {
  position: absolute;
  bottom: 32px;
  width: 100%;
  transition: opacity 0.3s, transform 0.3s, margin 0.3s;
}
.header__nav-list {
  position: relative;
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-wrap: nowrap;
  width: 100%;
  justify-content: space-between;
  font-weight: 600;
  font-size: 18px;
  line-height: 25px;
  text-transform: uppercase;
}
.header__nav-item {
  flex-shrink: 0;
  flex-grow: 1;
  text-align: center;
  border-right: 1px solid rgba(46, 50, 55, 0.1607843137);
}
.header__nav-item:first-child {
  border-left: none;
}
.header__nav-item:last-child {
  border-right: none;
}
.header__nav-link {
  flex-shrink: 0;
  font-family: Optima;
  font-size: 18px;
  font-weight: 700;
  line-height: 18px;
  color: #2E3237;
  transition: opacity 0.3s;
}
@media (hover: hover) {
  .header__nav-link:hover {
    opacity: 0.8;
  }
}
@media only screen and (max-width: 1000px) {
  .header__nav {
    position: relative;
    bottom: auto;
  }
  .header__nav-item {
    border: none;
  }
  .header__nav-link {
    border-bottom: 1px solid #2E3237;
    padding-bottom: 5px;
  }
  .header__nav-list {
    gap: 10px;
    flex-direction: column;
  }
}
.header__mobile-contacts {
  display: none;
  margin-top: 30px;
  padding-bottom: 30px;
}
.header__mobile-contacts .header__mail {
  display: flex;
  flex-direction: column;
  flex-shrink: 0;
  font-size: 14px;
  line-height: 22px;
  margin-bottom: 20px;
  margin-right: 0;
}
.header__mobile-contacts .header__mail a {
  font-weight: 600;
  color: #214250;
  text-align: center;
}
.header__mobile-contacts .header__mail span {
  color: #ffffff;
}
@media only screen and (max-width: 769px) {
  .header__mobile-contacts {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
}
.header__burger {
  position: absolute;
  right: 0;
  top: auto;
  flex-direction: column;
  justify-content: center;
  width: 46px;
  height: 40px;
  border-radius: 20%;
  background-color: #D8B075;
  flex-shrink: 0;
  padding: 4px 8px;
  z-index: 100;
  cursor: pointer;
  transition: background-color 0.3s;
  display: none;
}
.header__burger i {
  display: block;
  position: relative;
  height: 3px;
  width: 100%;
  border-radius: 2px;
  background: #fff;
  left: 0;
  top: 0;
  margin-bottom: 6px;
}
.header__burger i:first-child {
  width: 50%;
}
.header__burger i:last-child {
  margin-bottom: 0;
}
@media only screen and (max-width: 1000px) {
  .header__burger {
    display: flex;
  }
}
@media (hover: hover) {
  .header__burger:hover {
    background-color: #588d13;
  }
}
.header .search {
  position: relative;
  overflow: hidden;
  width: 378px;
  border-radius: 64px;
  border: 1px solid rgba(46, 50, 55, 0.3215686275);
  overflow: hidden;
  margin: 0 auto 0 24px;
  background-color: #ffffff;
}
.header .search button {
  position: absolute;
  z-index: 1;
  top: 0;
  right: 0;
  height: 100%;
  width: 60px;
  background-color: #ffffff;
}
.header .search span {
  display: block;
  height: 15px;
  width: 15px;
  margin-left: 10px;
}
.header .search input {
  width: 100%;
  padding: 20px 60px 20px 42px;
  border-radius: 64px;
  overflow: hidden;
  border: none;
  font-family: Onest;
  font-size: 14px;
  font-weight: 400;
  line-height: 17.85px;
  text-align: left;
  color: #2E3237;
}
@media only screen and (max-width: 1400px) {
  .header .search {
    width: 150px;
  }
  .header .search button {
    width: 40px;
  }
  .header .search input {
    padding: 12px 40px 12px 12px;
  }
}
@media only screen and (max-width: 1000px) {
  .header .search {
    margin: 0;
    width: 100%;
  }
}
.header .icons {
  display: flex;
  gap: 4px;
}
.header .icons__link {
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  background-color: #575A5F;
  border-radius: 50%;
  transition: background-color 0.3s;
}
@media (hover: hover) {
  .header .icons__link:hover {
    background-color: #3a3c3f;
  }
}
@media only screen and (max-width: 1400px) {
  .header .icons__link {
    width: 28px;
    height: 28px;
  }
}
.header .menu {
  display: contents;
}
@media only screen and (max-width: 1000px) {
  .header .menu {
    position: absolute;
    z-index: 99;
    top: 50px;
    left: -20px;
    background-color: rgba(231, 195, 140, 0.8901960784);
    backdrop-filter: blur(5px);
    display: flex;
    gap: 20px;
    flex-direction: column;
    align-items: center;
    height: 100vh;
    width: 100vw;
    padding: 24px 20px;
    pointer-events: none;
    transform: translateX(-100%);
    transition: transform 0.3s;
  }
}

body.is-nav-open {
  overflow: hidden;
}
body.is-nav-open .menu {
  transform: none;
  pointer-events: auto;
}
body.is-nav-open .header__burger {
  box-shadow: inset 0px 0px 10px 0px #74572d;
}
@media (hover: hover) {
  body.is-nav-open .header__burger:hover {
    background-color: #66a316;
  }
}

body.is-scrolled .header {
  transform: translateY(-76px);
  box-shadow: 0px 11px 15px -10px rgba(0, 0, 0, 0.7607843137);
}
body.is-scrolled .header__wrapper {
  position: relative;
  width: 100%;
  display: flex;
  padding: 36px 0 46px 0;
  align-items: center;
}
@media only screen and (max-width: 1000px) {
  body.is-scrolled .header__wrapper {
    padding: 10px 0;
  }
}
body.is-scrolled .header__nav {
  transform: translateY(20px);
}
@media only screen and (max-width: 1000px) {
  body.is-scrolled .header__nav {
    transform: none;
  }
}
@media only screen and (max-width: 1000px) {
  body.is-scrolled .header {
    transform: translateY(-50px);
  }
}
@media only screen and (max-width: 500px) {
  body.is-scrolled .header {
    transform: translateY(-44px);
  }
}
body.is-scrolled .main {
  padding-top: 120px;
}
body.is-scrolled .main--breadcrumbs {
  padding-top: 150px;
}
@media only screen and (max-width: 1000px) {
  body.is-scrolled .main {
    padding-top: 44px;
  }
}