.map {
  padding: 100px 0;
}
@media (max-width: 1700px) {
  .map {
    padding: 50px 0;
  }
}
.map--page {
  padding: 50px 0 100px 0;
}
@media (max-width: 1700px) {
  .map--page {
    padding: 50px 0;
  }
}
.map__header {
  position: relative;
  display: flex;
  gap: 32px;
  align-items: center;
  padding-bottom: 42px;
}
.map__header::before {
  position: absolute;
  z-index: 1;
  content: "";
  bottom: 1px;
  width: 60%;
  height: 1px;
  background-color: #D8B075;
}
.map__header::after {
  position: absolute;
  z-index: 2;
  content: "";
  bottom: 0;
  width: 30%;
  height: 3px;
  background-color: #AA804F;
}
.map__header-text {
  display: inline-block;
  width: 70%;
  font-family: Optima;
  font-size: 42px;
  font-weight: 400;
  line-height: 54.6px;
  letter-spacing: -0.01em;
  text-align: left;
  color: #181C1D;
  text-transform: uppercase;
}
@media (max-width: 900px) {
  .map__header {
    flex-direction: column;
  }
  .map__header::before {
    width: 100%;
  }
  .map__header::after {
    left: 0;
    width: 50%;
  }
  .map__header-value {
    font-size: 150px;
    font-weight: 400;
    line-height: 190px;
  }
  .map__header-text {
    font-size: 32px;
    line-height: 45px;
    width: 100%;
    text-align: center;
  }
}
.map__desktop {
  position: relative;
  display: flex;
  width: 1352px;
  height: 753px;
  transform: translateX(15px);
}
@media only screen and (max-width: 1300px) {
  .map__desktop {
    display: none;
  }
}
.map__mobile {
  padding: 30px 0;
  display: none;
}
@media only screen and (max-width: 1300px) {
  .map__mobile {
    display: block;
  }
}
.map__map {
  flex-shrink: 0;
  position: relative;
}
.map__map img {
  width: 1352px;
  height: 753px;
}
.map__marker {
  position: absolute;
  display: flex;
  top: 0;
  left: 0;
  width: 48px;
  height: 48px;
  border-radius: 50%;
  background-color: rgba(206, 76, 76, 0.3137254902);
  opacity: 0.6;
  cursor: pointer;
}
@media (hover: hover) {
  .map__marker:hover {
    opacity: 1;
  }
  .map__marker:hover .map__marker-label {
    opacity: 1;
  }
}
.map__marker::before {
  content: "";
  position: absolute;
  top: calc(50% - 6px);
  left: calc(50% - 6px);
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background-color: #fff;
  z-index: 2;
}
.map__marker::after {
  content: "";
  position: absolute;
  top: calc(50% - 15px);
  left: calc(50% - 15px);
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background-color: rgba(206, 76, 76, 0.6392156863);
}
