.map__marker-label {
  pointer-events: none;
  position: absolute;
  white-space: nowrap;
  z-index: 10;
  top: -18px;
  left: -10px;
  opacity: 0;
  font-family: Onest;
  font-size: 18px;
  font-weight: 500;
  line-height: 23px;
  text-align: center;
  color: rgba(24, 28, 29, 0.6392156863);
  padding: 0 8px;
  display: flex;
  flex-wrap: nowrap;
  background-color: #FFFFFF;
  border: 1px solid rgba(24, 28, 29, 0.6392156863);
  border-radius: 8px;
}
.map__list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-wrap: nowrap;
  gap: 8px;
  overflow-x: scroll;
  padding-bottom: 10px;
}
.map__item {
  background-color: #FFFFFF;
  border: 1px solid rgba(24, 28, 29, 0.6392156863);
  padding: 4px 8px;
  border-radius: 8px;
  font-family: Onest;
