.partner {
  padding: 100px 0;
}
@media only screen and (max-width: 1700px) {
  .partner {
    padding: 50px 0;
  }
}
.partner--page {
  padding: 50px 0;
}
.partner__header {
  position: relative;
  display: flex;
  gap: 32px;
  align-items: center;
  padding-bottom: 42px;
  margin-bottom: 53px;
}
.partner__header::before {
  position: absolute;
  z-index: 1;
  content: "";
  bottom: 1px;
  width: 60%;
  height: 1px;
  background-color: #D8B075;
}
.partner__header::after {
  position: absolute;
  z-index: 2;
  content: "";
  bottom: 0;
  width: 30%;
  height: 3px;
  background-color: #AA804F;
}
.partner__header-text {
  display: inline-block;
  width: 100%;
  margin-top: -18px;
  font-family: Optima;
  font-size: 42px;
  font-weight: 400;
  line-height: 54.6px;
  letter-spacing: -0.01em;
  text-align: left;
  color: #181C1D;
  text-transform: uppercase;
}
@media (max-width: 900px) {
  .partner__header {
    flex-direction: column;
  }
  .partner__header::before {
    width: 100%;
  }
  .partner__header::after {
    left: 0;
    width: 50%;
  }
  .partner__header-value {
    font-size: 150px;
    font-weight: 400;
    line-height: 190px;
  }
  .partner__header-text {
    font-size: 32px;
    line-height: 45px;
    width: 100%;
    text-align: center;
  }
}
.partner__wrapper {
  display: flex;
  width: 100%;
  gap: 96px;
}
@media only screen and (max-width: 1500px) {
  .partner__wrapper {
    gap: 36px;
    justify-content: space-between;
  }
}
@media only screen and (max-width: 1199px) {
  .partner__wrapper {
    flex-direction: column;
  }
}
.partner__content {
  display: flex;
  flex-direction: column;
}
.partner__list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  gap: 32px;
}
.partner__item {
  display: flex;
  gap: 16px;
}
.partner__icon {
  width: 32px;
  height: 32px;
}
.partner__text {
  font-family: Optima;
  font-size: 24px;
  font-weight: 400;
  line-height: 31.2px;
  text-align: left;
  color: rgba(46, 50, 55, 0.8588235294);
}

.partner__form {
  width: 532px;
  flex-shrink: 0;
  background-color: #F3F5F6;
  padding: 42px 72px;
  border-radius: 16px;
  margin-top: -20px;
}
@media only screen and (max-width: 1199px) {
  .partner__form {
    margin: 0;
    padding: 20px;
    width: 100%;
  }
}
.partner__form-heading {
  font-family: Optima;
  font-size: 24px;
  font-weight: 700;
  line-height: 27.58px;
  text-align: center;
  color: #667884;
  text-transform: uppercase;
  margin-bottom: 8px;
}
.partner__form-caption {
  display: inline-block;
  font-family: Onest;
  font-size: 16px;
  font-weight: 400;
  line-height: 20.4px;
  text-align: center;
  color: rgba(24, 28, 29, 0.4196078431);
  margin-bottom: 32px;
}