.footer {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #2E3237;
  color: rgba(243, 240, 226, 0.8588235294);
  width: 100%;
}
.footer__wrapper {
  display: flex;
  flex-direction: column;
  width: 100%;
}
.footer__main {
  padding: 100px 0;
}
@media only screen and (max-width: 1380px) {
  .footer__main {
    padding: 30px 0;
  }
}
.footer__logo {
  position: relative;
  padding-bottom: 32px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2392156863);
  display: inline-block;
  width: 220.64px;
}
.footer__logo::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 50%;
  height: 2px;
  background-color: #ffffff;
}
.footer__logo img {
  flex-shrink: 0;
  width: 220.64px;
  height: 37px;
}
@media only screen and (max-width: 610px) {
  .footer__logo {
    margin: 0 auto;
  }
}
.footer__time {
  display: flex;
  align-items: center;
  width: 100%;
  gap: 24px;
}
.footer__time-icon {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 68px;
  height: 68px;
  background-color: rgba(250, 249, 243, 0.0784313725);
  border-radius: 50%;
}
.footer__time-content {
  display: flex;
  flex-direction: column;
  color: rgba(255, 255, 255, 0.6392156863);
}
.footer__time-content h3 {
  color: #ffffff;
  margin-bottom: 8px;
  font-weight: 400;
}
.footer__requisites {
  list-style: none;
  padding: 0;
  margin: 0;
}
.footer__requisites-text {
  font-size: 14px;
  font-weight: 400;
  line-height: 18.2px;
  color: rgba(255, 255, 255, 0.6392156863) !important;
}
@media only screen and (max-width: 610px) {
  .footer__requisites {
    text-align: center;
  }
}
.footer__nav {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  gap: 24px;
}
.footer__nav-link {
  font-size: 16px;
  font-weight: 400;
  line-height: 20.4px;
  text-decoration-line: underline;
  color: rgba(243, 240, 226, 0.8588235294);
  transition: opacity 0.3s;
}
@media (hover: hover) {
  .footer__nav-link:hover {
    opacity: 0.7;
  }
}
.footer__address {
  font-style: normal;
  font-size: 14px;
  line-height: 22px;
}
@media only screen and (max-width: 900px) {
  .footer__address {
    text-align: center;
  }
}
.footer__map {
  border-radius: 16px;
  overflow: hidden;
  margin-top: 16px;
}
.footer__brands {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  gap: 24px;
}
.footer__brands-link {
  font-family: Optima;
  font-size: 16px;
  font-weight: 400;
  line-height: 16px;
  text-decoration-line: underline;
  color: #FFFFFF;
  transition: opacity 0.3s;
}
@media (hover: hover) {
  .footer__brands-link:hover {
    opacity: 0.7;
  }
}
@media only screen and (max-width: 769px) {
  .footer__brands-link {
    font-size: 12px;
    line-height: 16px;
  }
}
.footer__columns {
  display: flex;
  justify-content: space-between;
}
@media only screen and (max-width: 1100px) {
  .footer__columns {
    flex-wrap: wrap;
  }
}
@media only screen and (max-width: 610px) {
  .footer__columns {
    gap: 50px;
    justify-content: center;
  }
}
.footer__column {
  display: flex;
  flex-direction: column;
}
.footer__column--address {
  width: 360px;
}
.footer__column--info {
  gap: 42px;
}
@media only screen and (max-width: 900px) {
  .footer__column--address {
    width: 100%;
  }
}
.footer__group {
  display: contents;
}
@media only screen and (max-width: 1380px) {
  .footer__group--contacts {
    display: flex;
    flex-direction: column;
    gap: 30px;
  }
}
@media only screen and (max-width: 1080px) {
  .footer__group--links {
    display: flex;
    flex-direction: column;
    gap: 30px;
  }
}
@media only screen and (max-width: 900px) {
  .footer__group--contacts {
    width: 100%;
    margin-top: 50px;
  }
  .footer__group--links {
    display: flex;
    flex-direction: row;
    gap: 30px;
  }
}
@media only screen and (max-width: 610px) {
  .footer__group {
    text-align: center;
  }
  .footer__group--links {
    width: 100%;
    justify-content: center;
  }
  .footer__group--contacts {
    margin-top: 0;
  }
}
.footer__tel {
  display: flex;
  gap: 16px;
  margin-bottom: 16px;
}
.footer__tel span {
  font-size: 14px;
  line-height: 22px;
  color: #ffffff;
}
.footer__tel-list {
  display: flex;
  flex-direction: column;
}
.footer__tel-list a {
  font-weight: 600;
  font-size: 18px;
  line-height: 25px;
  color: #ffffff;
  transition: opacity 0.3s;
}
@media (hover: hover) {
  .footer__tel-list a:hover {
    opacity: 0.7;
  }
}
@media only screen and (max-width: 900px) {
  .footer__tel {
    justify-content: center;
    align-items: center;
  }
}
.footer__mail {
  font-size: 14px;
  font-weight: 400;
  line-height: 17.85px;
  color: #9B9B9B;
  transition: opacity 0.3s;
  margin-left: auto;
}
@media (hover: hover) {
  .footer__mail:hover {
    opacity: 0.7;
  }
}
@media only screen and (max-width: 900px) {
  .footer__mail {
    margin-left: 0;
    text-align: center;
  }
}
.footer__controls {
  display: flex;
  gap: 8px;
  flex-direction: column;
  margin-top: 16px;
}
.footer__button {
  text-transform: uppercase;
}
.footer__bottom {
  width: 100%;
  padding: 24px 0;
  border-top: 1px solid rgba(255, 255, 255, 0.1607843137);
  color: rgba(255, 255, 255, 0.3215686275);
  font-size: 14px;
  font-weight: 400;
  line-height: 18.2px;
}
.footer__bottom a {
  color: rgba(255, 255, 255, 0.3215686275);
  font-size: 14px;
  font-weight: 400;
  line-height: 18.2px;
  transition: opacity 0.3s;
}
@media (hover: hover) {
  .footer__bottom a:hover {
    opacity: 0.7;
  }
}
.footer__bottom-content {
  display: flex;
  align-items: center;
  width: 100%;
  justify-content: space-between;
}
@media only screen and (max-width: 900px) {
  .footer__bottom-content {
    width: 100%;
    flex-direction: column;
    gap: 5px;
  }
}
.footer__bottom-privacy {
  margin-right: 100px;
}
@media only screen and (max-width: 1160px) {
  .footer__bottom-privacy {
    margin: 0;
  }
}
.footer__bottom-studio {
  display: flex;
  align-items: center;
}
.footer__bottom-studio img {
  margin-top: 2px;
  height: 11px;
  width: 56px;
}
.footer__bottom-studio span {
  padding-right: 14px;
}
@media only screen and (max-width: 1160px) {
  .footer__bottom-studio {
    margin: 0;
  }
}
.footer .icons {
  display: flex;
  gap: 4px;
}
.footer .icons__link {
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  background-color: #575A5F;
  border-radius: 50%;
  transition: background-color 0.3s;
}
@media (hover: hover) {
  .footer .icons__link:hover {
    background-color: #3a3c3f;
  }
}