.about__text {
  font-family: Onest;
  font-size: 18px;
  font-weight: 400;
  line-height: 25.2px;
  text-align: left;
  color: rgba(24, 28, 29, 0.6392156863);
}
.about__wrapper {
  display: flex;
  gap: 96px;
  margin-top: 64px;
}
@media only screen and (max-width: 1500px) {
  .about__wrapper {
    gap: 36px;
  }
}
@media only screen and (max-width: 1200px) {
  .about__wrapper {
    flex-direction: column;
  }
}
.about__caption {
  font-family: Optima;
  font-size: 24px;
  font-weight: 400;
  line-height: 26.71px;
  text-align: left;
  text-transform: uppercase;
  color: #181C1D;
  margin-top: 42px;
}
.about__certificates {
  display: flex;
  flex-wrap: wrap;
  gap: 16px;
  margin: 34px 0;
}
.about__certificate {
  position: relative;
  width: 23%;
  height: 218px;
  border: 1px solid rgba(46, 50, 55, 0.1607843137);
  border-radius: 16px;
  overflow: hidden;
}
.about__certificate img {
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: top center;
}
.about__certificate::before {
  content: "";
  position: absolute;
  z-index: 1;
  inset: 0;
  width: 100%;
  height: 100%;
  background-image: url("../img/certificates/zoom.svg");
  background-size: 32px;
  background-repeat: no-repeat;
  background-position: center center;
  background-color: rgba(46, 50, 55, 0.6392156863);
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.3s;
}
@media only screen and (max-width: 1100px) {
  .about__certificate {
    width: 31%;
  }
}
@media only screen and (max-width: 750px) {
  .about__certificate {
    width: 48%;
  }
}
@media only screen and (max-width: 500px) {
  .about__certificate {
    width: 100%;
  }
}
@media (hover: hover) {
  .about__certificate:hover::before {
    opacity: 1;
    pointer-events: auto;
  }
}
.about .contacts__form {
  max-height: 752px;
  margin-top: -150px;
}
@media only screen and (max-width: 1200px) {
  .about .contacts__form {
    margin-top: 0;
  }
}