.payment__wrapper {
  display: flex;
  justify-content: space-between;
}
@media (max-width: 1310px) {
  .payment__wrapper {
    flex-direction: column;
    gap: 24px;
  }
}
.payment__caption {
  font-family: Optima;
  font-size: 24px;
  font-weight: 400;
  line-height: 31.2px;
  text-align: left;
  margin-bottom: 32px;
}
.payment__list {
  padding-left: 30px;
  margin: 0;
  display: flex;
  flex-direction: column;
  gap: 32px;
}
.payment__item-text {
  font-family: Onest;
  font-size: 18px;
  font-weight: 500;
  line-height: 25.2px;
  text-align: left;
  color: rgba(24, 28, 29, 0.8588235294);
}
.payment__data {
  list-style: none;
  padding: 0;
  margin: 0;
  margin-top: 32px;
}
.payment__data-value {
  font-family: Onest;
  font-size: 18px;
  font-weight: 400;
  line-height: 25.2px;
  text-align: left;
  color: rgba(24, 28, 29, 0.6392156863);
}
.payment__company {
  display: flex;
  flex-wrap: wrap;
  gap: 16px;
  max-width: 857px;
}
@media (max-width: 1310px) {
  .payment__company {
    width: 100%;
    max-width: 100%;
  }
}
@media (max-width: 900px) {
  .payment__company {
    flex-wrap: nowrap;
    overflow-x: scroll;
  }
}
.payment__company-item {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 23%;
  height: 150px;
  background-color: rgba(239, 240, 242, 0.3215686275);
  border-radius: 16px;
}
.payment__company-item img {
  width: 50%;
  height: 50%;
  object-fit: contain;
}
@media (max-width: 900px) {
  .payment__company-item {
    width: 200px;
    flex-shrink: 0;
    height: 130px;
  }
}