.assortment {
  padding: 100px 0;
}
@media (max-width: 1700px) {
  .assortment {
    padding: 50px 0;
  }
}
.assortment__header {
  position: relative;
  display: flex;
  gap: 32px;
  align-items: center;
  padding-bottom: 42px;
  margin-bottom: 53px;
}
.assortment__header::before {
  position: absolute;
  z-index: 1;
  content: "";
  bottom: 1px;
  width: 60%;
  height: 1px;
  background-color: #D8B075;
}
.assortment__header::after {
  position: absolute;
  z-index: 2;
  content: "";
  bottom: 0;
  width: 30%;
  height: 3px;
  background-color: #AA804F;
}
.assortment__header-text {
  display: inline-block;
  width: 70%;
  font-family: Optima;
  font-size: 42px;
  font-weight: 400;
  line-height: 54.6px;
  letter-spacing: -0.01em;
  text-align: left;
  color: #181C1D;
  text-transform: uppercase;
}
@media (max-width: 900px) {
  .assortment__header {
    flex-direction: column;
  }
  .assortment__header::before {
    width: 100%;
  }
  .assortment__header::after {
    left: 0;
    width: 50%;
  }
  .assortment__header-value {
    font-size: 150px;
    font-weight: 400;
    line-height: 190px;
  }
  .assortment__header-text {
    font-size: 32px;
    line-height: 45px;
    width: 100%;
    text-align: center;
  }
}
.assortment__list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(3, 1fr);
  grid-column-gap: 16px;
  grid-row-gap: 16px;
}
@media (max-width: 1200px) {
  .assortment__list {
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, 1fr);
  }
}
@media (max-width: 800px) {
  .assortment__list {
    grid-template-columns: repeat(1, 1fr);
    grid-template-rows: repeat(1, 1fr);
  }
}
.assortment__item {
  position: relative;
  height: 400px;
  padding: 42px;
  border-radius: 16px;
  overflow: hidden;
  background-color: #EFDFC8;
}
@media (max-width: 1300px) {
  .assortment__item {
    height: 300px;
    padding: 20px;
  }
}
@media (max-width: 900px) {
  .assortment__item {
    height: 220px;
  }
}
.assortment__item::before {
  content: "";
  position: absolute;
  bottom: -40%;
  left: -30%;
  width: 390px;
  height: 390px;
  border-radius: 50%;
  background-color: rgba(211, 131, 85, 0.1215686275);
  transition: left 0.3s, bottom 0.3s, right 0.3s, top 0.3s;
}
.assortment__item-header {
  display: flex;
  align-items: center;
  gap: 12px;
  margin-bottom: 16px;
}
.assortment__item-icon {
  width: 28px;
  height: 28px;
  border-radius: 50%;
  overflow: hidden;
}
.assortment__item-name {
  font-family: Optima;
  font-size: 24px;
  font-weight: 700;
  line-height: 24px;
  text-transform: uppercase;
  text-align: left;
  text-underline-position: from-font;
  text-decoration-skip-ink: none;
  color: #2E3237;
}
.assortment__item-text {
  font-family: Onest;
  font-size: 18px;
  font-weight: 400;
  line-height: 22.95px;
  text-align: left;
  color: rgba(24, 28, 29, 0.6392156863);
  transition: opacity 0.3s;
  opacity: 0;
}
.assortment__item-img {
  position: absolute;
  right: 0;
  bottom: 0;
  height: 65%;
  transition: left 0.3s, bottom 0.3s, right 0.3s, top 0.3s;
}
.assortment__item-img--top {
  z-index: 2;
  right: 20%;
}
.assortment__item-img--bottom {
  z-index: 1;
  height: 70%;
}
@media (hover: hover) {
  .assortment__item:hover::before {
    bottom: -60%;
    left: -50%;
  }
  .assortment__item:hover .assortment__item-text {
    opacity: 1;
  }
  .assortment__item:hover .assortment__item-img--top {
    bottom: -20%;
  }
  .assortment__item:hover .assortment__item-img--bottom {
    right: -15%;
  }
}
.assortment__item--10th {
  background-color: rgba(102, 120, 132, 0.168627451);
}
.assortment__item--10th::before {
  background-color: rgba(102, 120, 132, 0.1019607843);
}
.assortment__item--10th .assortment__item-img--top {
  right: 40px;
}
.assortment__item--galaxy {
  background-color: #F3F0E2;
}
.assortment__item--galaxy::before {
  background-color: rgba(102, 120, 132, 0.1019607843);
}
.assortment__item--galaxy .assortment__item-img--top {
  right: 0;
}
.assortment__item--areen {
  background-color: #F3F0E2;
}
.assortment__item--areen::before {
  background-color: rgba(102, 120, 132, 0.1019607843);
}
.assortment__item--areen .assortment__item-img--top {
  right: 40px;
  bottom: 20px;
}
.assortment__item--shirley {
  background-color: #F3F0E2;
}
.assortment__item--shirley::before {
  background-color: rgba(102, 120, 132, 0.1019607843);
}
.assortment__item--cindy {
  background-color: rgba(102, 120, 132, 0.168627451);
}
.assortment__item--cindy::before {
  background-color: rgba(102, 120, 132, 0.1019607843);
}
.assortment__item--giorgio {
  background-color: rgba(102, 120, 132, 0.168627451);
}
.assortment__item--giorgio::before {
  background-color: rgba(102, 120, 132, 0.1019607843);
}
.assortment__item--giorgio .assortment__item-img--top {
  right: 20px;
  bottom: 0;
}