@charset "UTF-8";
.hero {
  width: 100%;
  min-height: 100vh;
  background-image: url("../img/hero-bg.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center top;
  padding: 160px 0;
}
@media (max-width: 1700px) {
  .hero {
    padding: 100px 0;
  }
}
@media (max-width: 1100px) {
  .hero {
    padding: 40px 0;
  }
}
.hero__wrapper {
  width: 55%;
  display: flex;
  flex-direction: column;
}
@media (max-width: 1400px) {
  .hero__wrapper {
    width: 70%;
  }
}
@media (max-width: 1100px) {
  .hero__wrapper {
    width: 100%;
  }
}
.hero__caption {
  display: flex;
  flex-direction: column;
}
.hero__title {
  font-family: Peignot;
  font-size: 96px;
  font-weight: 400;
  line-height: 100.32px;
  text-align: left;
  color: #181C1D;
}
@media (max-width: 1100px) {
  .hero__title {
    font-size: 56px;
    line-height: 60px;
  }
}
.hero__subtitle {
  font-family: Onest;
  font-size: 24px;
  font-weight: 500;
  line-height: 30.6px;
  letter-spacing: 0.03em;
  text-align: left;
}
@media (max-width: 1100px) {
  .hero__subtitle {
    font-size: 14px;
    line-height: 18px;
  }
}
.hero__list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  column-gap: 42px;
  row-gap: 12px;
  width: 100%;
  flex-wrap: wrap;
  margin-top: 42px;
}
.hero__item {
  display: flex;
  align-items: center;
  position: relative;
  width: 47%;
  padding-left: 20px;
  font-family: Onest;
  font-size: 18px;
  font-weight: 400;
  line-height: 22.95px;
  text-align: left;
  color: rgba(46, 50, 55, 0.9607843137);
}
.hero__item::before {
  content: "";
  position: absolute;
  left: 0;
  top: calc(50% - 4px);
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background-color: #667884;
}
@media (max-width: 1100px) {
  .hero__item {
    width: 100%;
  }
}
.hero__button {
  margin-top: 64px;
  width: 60%;
  background-color: #CE4C4C;
  color: #ffffff;
  text-transform: uppercase;
  padding: 32px;
}
.hero__button .icon {
  width: 22px;
  height: 22px;
  border: 1px solid #ffffff;
  padding-left: 2px;
  margin-left: 22px;
}
@media (hover: hover) {
  .hero__button:hover {
    background-color: #a12c2c;
    color: #ffffff;
  }
}
@media (max-width: 1100px) {
  .hero__button {
    margin-top: 24px;
  }
}
@media (max-width: 600px) {
  .hero__button {
    width: 100%;
  }
}
.hero__slider {
  margin-top: 100px;
  /* Последний слайд не будет иметь правого отступа */
}
.hero__slider .slick-list {
  margin-right: 2px;
}
.hero__slider .slick-list .slick-track > div:last-child {
  margin-right: 0;
}
.hero__slider .slick-slide {
  height: 194px;
  background-color: rgba(243, 240, 226, 0.5098039216);
  border-radius: 16px;
  backdrop-filter: blur(18px);
  box-shadow: 0px 4px 32px 0px rgba(170, 128, 79, 0.3215686275);
  margin-right: 16px; /* Отступ между слайдами */
}
.hero__slider .slick-track {
  height: 240px;
}
.hero__slider .slick-dots {
  list-style: none; /* Убираем стандартные маркеры списка */
  display: flex; /* Используем flex для выравнивания точек */
  justify-content: center; /* Центрируем точки горизонтально */
  gap: 8px; /* Расстояние между точками */
  padding: 0; /* Убираем отступы */
  margin: 0; /* Убираем внешние отступы */
}
.hero__slider .slick-dots li button::before {
  content: "";
  width: 8px; /* Размер точки */
  height: 8px;
  background-color: rgba(255, 255, 255, 0.6392156863); /* Цвет неактивной точки */
  border-radius: 50%; /* Круглая форма */
  display: inline-block;
  transition: background-color 0.3s;
}
.hero__slider .slick-dots button {
  width: 8px; /* Ширина кнопки */
  height: 8px; /* Высота кнопки */
  border-radius: 50%; /* Делаем кнопки круглыми */
  background-color: rgba(255, 255, 255, 0.6392156863); /* Цвет неактивных кнопок */
  cursor: pointer; /* Указатель мыши при наведении */
  padding: 0; /* Убираем внутренние отступы */
  border: none; /* Убираем границы */
  font-size: 0;
}
.hero__slider .slick-dots .slick-active button::before { /* Стилизация активной точки */
  background-color: #AA804F; /* Цвет активной точки */
}
@media (max-width: 1400px) {
  .hero__slider {
    margin-top: 50px;
  }
}
.hero__slide {
  display: flex;
  padding: 16px;
  gap: 20px;
}
.hero__slide-img {
  width: 160px;
  height: 160px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.hero__slide-img img {
  height: 153px;
  object-fit: contain;
}
.hero__slide-content {
  display: flex;
  flex-direction: column;
  padding: 16px 24px 16px 0;
}
.hero__slide-caption, .hero__slide-name {
  font-family: Optima;
  font-size: 18px;
  font-weight: 400;
  line-height: 21.6px;
  text-align: left;
}
.hero__slide-name {
  font-weight: 700;
}
.hero__slide-link {
  margin-top: auto;
  background-color: rgba(216, 176, 117, 0.6392156863);
  color: #755530;
  text-transform: uppercase;
  padding: 16px;
}
@media (hover: hover) {
  .hero__slide-link:hover {
    background-color: rgba(180, 128, 51, 0.6392156863);
    color: #ffffff;
  }
}