.button {
  display: inline-flex;
  align-items: center;
  position: relative;
  text-align: center;
  justify-content: center;
  overflow: hidden;
  cursor: pointer;
  text-decoration: none;
  transition: color 0.3s, background-color 0.3s, opacity 0.3s, box-shadow 0.3s;
  font-family: Onest;
  font-size: 14px;
  font-weight: 600;
  line-height: 17.85px;
  text-transform: uppercase;
  color: #667884;
  background-color: rgba(102, 120, 132, 0.3215686275);
  padding: 20px 40px;
  border-radius: 64px;
  border: none;
}
@media (hover: hover) {
  .button:hover {
    background-color: rgba(35, 42, 46, 0.3215686275);
    color: #ffffff;
  }
}
@media only screen and (max-width: 1000px) {
  .button {
    background-color: #667884;
    color: #ffffff;
  }
}
.button--light {
  color: #CADFED;
  background-color: #667884;
}
@media (hover: hover) {
  .button--light:hover {
    background-color: #505e67;
    color: #ffffff;
  }
}
.button--yellow {
  background-color: #D8B075;
  color: #755530;
}
@media (hover: hover) {
  .button--yellow:hover {
    background-color: #8c6428;
    color: #ffffff;
  }
}
@media only screen and (max-width: 1400px) {
  .button {
    padding: 10px 20px;
    font-size: 13px;
    font-weight: 500;
    line-height: 16px;
  }
}