@charset "UTF-8";
.catalog {
  padding: 50px 0;
}
.catalog__header {
  position: relative;
  display: flex;
  gap: 32px;
  align-items: center;
  padding-bottom: 42px;
  margin-bottom: 53px;
}
.catalog__header::before {
  position: absolute;
  z-index: 1;
  content: "";
  bottom: 1px;
  width: 60%;
  height: 1px;
  background-color: #D8B075;
}
.catalog__header::after {
  position: absolute;
  z-index: 2;
  content: "";
  bottom: 0;
  width: 30%;
  height: 3px;
  background-color: #AA804F;
}
.catalog__header-text {
  display: inline-block;
  width: 50%;
  margin-top: -18px;
  font-family: Optima;
  font-size: 42px;
  font-weight: 400;
  line-height: 54.6px;
  letter-spacing: -0.01em;
  text-align: left;
  color: #181C1D;
  text-transform: uppercase;
}
@media (max-width: 900px) {
  .catalog__header {
    flex-direction: column;
  }
  .catalog__header::before {
    width: 100%;
  }
  .catalog__header::after {
    left: 0;
    width: 50%;
  }
  .catalog__header-value {
    font-size: 150px;
    font-weight: 400;
    line-height: 190px;
  }
  .catalog__header-text {
    font-size: 32px;
    line-height: 45px;
    width: 100%;
    text-align: center;
  }
}
.catalog__wrapper {
  display: flex;
  gap: 48px;
  justify-content: space-between;
}
@media (max-width: 1300px) {
  .catalog__wrapper {
    gap: 28px;
  }
}
@media (max-width: 900px) {
  .catalog__wrapper {
    display: contents;
  }
}
.catalog__menu {
  flex-shrink: 0;
  width: 364px;
  max-height: 1200px;
  padding: 32px;
  border-radius: 16px;
  background-color: #F5F6F7;
  display: flex;
  flex-direction: column;
  gap: 42px;
}
.catalog__menu-caption {
  font-family: Optima;
  text-transform: uppercase;
  font-size: 18px;
  font-weight: 700;
  line-height: 23.4px;
  text-align: left;
  color: rgba(46, 50, 55, 0.8588235294);
  margin-bottom: 24px;
}
.catalog__menu-list {
  display: flex;
  flex-direction: column;
  gap: 16px;
}
.catalog__menu-item {
  display: flex;
  gap: 12px;
  align-items: center;
}
.catalog__menu-item.is-current .catalog__menu-brand {
  color: #AA804F;
}
.catalog__menu-icon {
  width: 16px;
  height: 16px;
  border-radius: 50%;
  overflow: hidden;
}
.catalog__menu-icon svg {
  object-fit: contain;
}
.catalog__menu-brand, .catalog__menu-country {
  font-family: Onest;
  font-size: 16px;
  font-weight: 400;
  line-height: 20.4px;
  text-align: left;
}
.catalog__menu-brand {
  color: rgba(24, 28, 29, 0.6392156863);
}
.catalog__menu-brand.is-current {
  color: #AA804F;
}
.catalog__menu-country {
  color: rgba(24, 28, 29, 0.3215686275);
}
@media (max-width: 1400px) {
  .catalog__menu {
    width: 330px;
    padding: 32px 16px;
  }
}
@media (max-width: 900px) {
  .catalog__menu {
    position: fixed;
    width: 300px;
    height: 70vh;
    z-index: 99;
    top: 130px;
    pointer-events: none;
    transition: transform 0.3s;
    transform: translateX(-110%);
    box-shadow: 0px 4px 16px 0px rgba(119, 119, 119, 0.6);
  }
}
.catalog__list {
  display: grid;
  gap: 16px;
  grid-template-columns: repeat(3, minmax(auto, 330px));
  list-style-type: none;
  padding: 0;
}
@media (max-width: 1400px) {
  .catalog__list {
    grid-template-columns: repeat(2, minmax(auto, 330px));
  }
}
@media (max-width: 900px) {
  .catalog__list {
    grid-template-columns: repeat(3, minmax(auto, auto));
  }
}
@media (max-width: 800px) {
  .catalog__list {
    grid-template-columns: repeat(2, minmax(auto, auto));
  }
}
@media (max-width: 500px) {
  .catalog__list {
    grid-template-columns: repeat(1, minmax(auto, auto));
  }
}
.catalog__list--news {
  grid-template-columns: repeat(4, minmax(auto, 330px));
}
@media (max-width: 1300px) {
  .catalog__list--news {
    grid-template-columns: repeat(3, minmax(auto, auto));
  }
}
@media (max-width: 900px) {
  .catalog__list--news {
    grid-template-columns: repeat(2, minmax(auto, auto));
  }
}
@media (max-width: 650px) {
  .catalog__list--news {
    grid-template-columns: repeat(1, minmax(auto, auto));
  }
}
.catalog__item {
  display: flex;
  flex-direction: column;
  font-family: Optima;
  font-weight: 400;
  font-size: 20px;
  line-height: 24px;
  text-align: center;
  padding: 16px;
  border-radius: 16px;
  background-color: rgba(239, 240, 242, 0.3215686275);
}
@media (max-width: 900px) {
  .catalog__item {
    width: 100%;
    font-size: 14px;
    line-height: 20px;
  }
}
.catalog__item-description {
  font-size: 20px;
  line-height: 24px;
}
.catalog__item-name {
  font-family: Optima;
  font-weight: 700;
  margin-bottom: 16px;
}
.catalog__item-img {
  margin-top: auto;
  margin-bottom: 32px;
  position: relative;
  width: 100%;
  border-radius: 8px;
  overflow: hidden;
}
.catalog__item-img img {
  width: 100%;
  height: 100%;
  inset: 0;
  object-fit: cover;
}
.catalog__item-button {
  width: 100%;
  height: 50px;
  color: #fff;
  background-color: #CE4C4C;
}
@media (hover: hover) {
  .catalog__item-button:hover {
    background-color: #a12c2c;
  }
}
.catalog__item-news-caption {
  font-family: Optima;
  font-size: 20px;
  font-weight: 400;
  line-height: 24px;
  text-align: left;
  color: #2E3237;
}
.catalog__item-news-text {
  font-family: Onest;
  font-size: 16px;
  font-weight: 400;
  line-height: 22.4px;
  text-align: left;
  color: rgba(24, 28, 29, 0.3215686275);
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 3; /* Здесь указываем количество строк */
  overflow: hidden;
  text-overflow: ellipsis; /* Добавляет многоточие в конце */
}
.catalog__item-news-button {
  font-family: Onest;
  font-size: 16px;
  font-weight: 400;
  line-height: 22.4px;
  text-align: left;
  text-decoration-line: underline;
  color: #AA804F;
  margin-top: auto;
}
.catalog__item-news-link .catalog__item-img {
  margin-top: 0;
}
.catalog__item-news-link .catalog__item-img img {
  transition: transform 0.3s;
}
@media (hover: hover) {
  .catalog__item-news-link:hover {
    background-color: rgba(197, 200, 208, 0.3215686275);
  }
  .catalog__item-news-link:hover .catalog__item-img img {
    transform: scale(1.2);
  }
}
.catalog .form {
  align-items: flex-start;
  gap: 42px;
}
.catalog .form .catalog__menu-block {
  display: flex;
  flex-direction: column;
  gap: 16px;
  width: 100%;
}
.catalog .form .catalog__menu-block button {
  width: 100%;
}
.catalog .form .form__button--reset {
  background-color: #E5E8EA;
  color: #AEB7BE;
}
@media (hover: hover) {
  .catalog .form .form__button--reset:hover {
    background-color: #505e67;
    color: #ffffff;
  }
}
.catalog .form .form__label--checkbox {
  margin-top: 0;
  font-family: Onest;
  font-size: 16px;
  font-weight: 400;
  line-height: 20.4px;
  text-align: left;
  color: rgba(24, 28, 29, 0.6392156863);
}

.catalog__filters {
  display: none;
  padding: 10px 16px;
  background-color: #bdc0c2;
  position: fixed;
  opacity: 1;
  transition: opacity 0.3s;
  z-index: 99;
  left: 20px;
  top: 70px;
  transition: box-shadow 0.3s;
  box-shadow: 0px 4px 6px 0px rgba(119, 119, 119, 0.6);
}
@media (max-width: 900px) {
  .catalog__filters {
    display: inline-block;
  }
}

body.is-nav-open .catalog__filters {
  opacity: 0;
  pointer-events: none;
}

body.is-filters-open {
  overflow: hidden;
}
body.is-filters-open .catalog__filters {
  box-shadow: inset 0px 0px 12px 2px rgba(65, 65, 65, 0.6);
}
@media (max-width: 900px) {
  body.is-filters-open .catalog__menu {
    transform: none;
    pointer-events: auto;
  }
}