.breadcrumbs {
  margin-top: 20px;
}
@media (max-width: 1000px) {
  .breadcrumbs {
    display: none;
  }
}
.breadcrumbs a {
  font-size: 14px;
  line-height: 20px;
  color: #667884;
}
.breadcrumbs .breadcrumb_font_style {
  padding: 6px;
  border-radius: 4px;
  background-color: rgba(155, 155, 155, 0.1607843137);
  margin-right: 10px;
}
.breadcrumbs .breadcrumb_font_dash {
  color: rgba(46, 50, 55, 0.1607843137);
}
.breadcrumbs ol {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  align-items: center;
  width: max-content;
  font-size: 14px;
  line-height: 20px;
  color: #667884;
}
.breadcrumbs ol li:not(:last-child) {
  margin-right: 10px;
}
.breadcrumbs ol li:last-child .breadcrumb_font_style {
  padding: 6px;
  border-radius: 4px;
  background-color: rgba(155, 155, 155, 0.1607843137);
  margin-right: 10px;
  color: rgba(46, 50, 55, 0.3215686275);
}
.breadcrumbs .span_m_l_20 {
  margin-left: 15px;
}