.contacts {
  padding: 50px 0;
}
.contacts__header {
  position: relative;
  display: flex;
  gap: 32px;
  align-items: center;
  padding-bottom: 42px;
  margin-bottom: 53px;
}
.contacts__header::before {
  position: absolute;
  z-index: 1;
  content: "";
  bottom: 1px;
  width: 60%;
  height: 1px;
  background-color: #D8B075;
}
.contacts__header::after {
  position: absolute;
  z-index: 2;
  content: "";
  bottom: 0;
  width: 30%;
  height: 3px;
  background-color: #AA804F;
}
.contacts__header-text {
  display: inline-block;
  width: 70%;
  font-family: Optima;
  font-size: 42px;
  font-weight: 400;
  line-height: 54.6px;
  letter-spacing: -0.01em;
  text-align: left;
  color: #181C1D;
  text-transform: uppercase;
}
@media (max-width: 900px) {
  .contacts__header {
    flex-direction: column;
  }
  .contacts__header::before {
    width: 100%;
  }
  .contacts__header::after {
    left: 0;
    width: 50%;
  }
  .contacts__header-value {
    font-size: 150px;
    font-weight: 400;
    line-height: 190px;
  }
  .contacts__header-text {
    font-size: 32px;
    line-height: 45px;
    width: 100%;
    text-align: center;
  }
}
.contacts__wrapper {
  display: flex;
  gap: 96px;
  margin-top: 64px;
}
@media only screen and (max-width: 1500px) {
  .contacts__wrapper {
    gap: 36px;
  }
}
@media only screen and (max-width: 1200px) {
  .contacts__wrapper {
    flex-direction: column-reverse;
  }
}
.contacts__content {
  display: flex;
  flex-direction: column;
}
.contacts__data {
  display: flex;
  justify-content: space-between;
  margin-bottom: 32px;
}
@media only screen and (max-width: 1500px) {
  .contacts__data {
    flex-wrap: wrap;
    gap: 24px;
  }
}
.contacts__data-name {
  font-family: Optima;
  font-size: 22px;
  font-weight: 700;
  line-height: 25.28px;
  text-align: left;
  color: #AA804F;
  text-transform: uppercase;
}
.contacts__phone {
  display: flex;
  flex-direction: column;
}
.contacts__phone .icons {
  display: flex;
  gap: 4px;
}
.contacts__phone .icons__link {
  width: 32px;
  height: 32px;
  background-color: #575A5F;
}
.contacts__phone-wrapper {
  display: flex;
  gap: 16px;
  margin-right: 32px;
  margin-top: 16px;
}
.contacts__phone-list {
  display: flex;
  gap: 4px;
  flex-direction: column;
}
.contacts__phone-link {
  font-family: Onest;
  font-size: 16px;
  font-weight: 600;
  line-height: 20.4px;
  text-align: left;
  color: #2E3237;
  transition: opacity 0.3s;
}
@media (hover: hover) {
  .contacts__phone-link:hover {
    opacity: 0.7;
  }
}
.contacts__address {
  display: flex;
  flex-direction: column;
}
.contacts__address address {
  margin-top: 16px;
  margin-bottom: 8px;
  font-style: normal;
  font-size: 14px;
  line-height: 22px;
}
.contacts__mail {
  font-family: Onest;
  font-size: 14px;
  font-weight: 400;
  line-height: 17.85px;
  text-align: left;
  text-decoration-line: underline;
  text-decoration-style: solid;
  color: #AA804F;
  transition: opacity 0.3s;
}
@media (hover: hover) {
  .contacts__mail:hover {
    opacity: 0.7;
  }
}
.contacts__map-wrapper {
  display: flex;
  height: 510px;
  width: 100%;
  border-radius: 16px;
  overflow: hidden;
}
.contacts__rec {
  display: inline-block;
  margin-top: 32px;
  font-family: Onest;
  font-size: 14px;
  font-weight: 400;
  line-height: 17.85px;
  text-align: left;
  color: rgba(24, 28, 29, 0.3215686275);
}
.contacts__time {
  display: flex;
  flex-direction: column;
}
.contacts__time-content {
  display: inline-block;
  margin-top: 16px;
  font-family: Onest;
  font-size: 16px;
  font-weight: 400;
  line-height: 20.4px;
  text-align: left;
  color: rgba(24, 28, 29, 0.6392156863);
}

.contacts__form {
  width: 532px;
  flex-shrink: 0;
  background-color: #F3F5F6;
  padding: 42px 72px;
  border-radius: 16px;
  margin-top: 0;
}
@media only screen and (max-width: 1500px) {
  .contacts__form {
    width: 33.3333%;
    padding: 20px;
    gap: 24px;
  }
}
@media only screen and (max-width: 1200px) {
  .contacts__form {
    width: 100%;
  }
}
.contacts__form-heading {
  font-family: Optima;
  font-size: 24px;
  font-weight: 700;
  line-height: 27.58px;
  text-align: center;
  color: #667884;
  text-transform: uppercase;
  margin-bottom: 8px;
}
.contacts__form-caption {
  display: inline-block;
  font-family: Onest;
  font-size: 16px;
  font-weight: 400;
  line-height: 20.4px;
  text-align: center;
  color: rgba(24, 28, 29, 0.4196078431);
  margin-bottom: 32px;
  width: 100%;
  text-align: center;
}
.contacts__form textarea {
  border-radius: 16px;
  height: 248px;
  border: none;
  background-color: #ffffff;
  padding: 24px 42px;
  border: 1px solid rgba(46, 50, 55, 0.1607843137);
}
.contacts__form .form__button {
  margin-top: 34px;
}