.form {
  display: flex;
  flex-direction: column;
  gap: 8px;
}
.form__input {
  width: 100%;
}
.form__input-custom {
  position: relative;
  display: inline-flex;
  margin: 32px 0;
}
.form__button {
  width: 100%;
  background-color: #CE4C4C;
  text-transform: uppercase;
  color: #ffffff;
}
@media (hover: hover) {
  .form__button:hover {
    background-color: #a12c2c;
  }
}
.form__checkbox {
  display: none;
}
.form__wrapper--agreement {
  opacity: 0.5;
}
.form textarea {
  resize: none;
}
.form textarea::-webkit-scrollbar, .form textarea::-webkit-scrollbar-thumb {
  display: none;
}
.form input {
  border: none;
  background-color: #ffffff;
  border-radius: 64px;
  padding: 24px 42px;
  border: 1px solid rgba(46, 50, 55, 0.1607843137);
}
.form__label {
  display: block;
  cursor: pointer;
}
.form__label-custom {
  font-family: Onest;
  font-size: 16px;
  font-weight: 400;
  line-height: 20.4px;
  text-align: center;
  margin: 0 auto;
  color: #667884;
  border-bottom: 1px dashed #667884;
  transition: border-color 0.3s;
  cursor: pointer;
}
@media (hover: hover) {
  .form__label-custom:hover {
    border-color: transparent;
  }
}
.form__label--checkbox {
  color: rgba(24, 28, 29, 0.6392156863);
  font-family: Onest;
  font-size: 12px;
  font-weight: 400;
  line-height: 15.3px;
  text-align: center;
  margin-top: 16px;
}
.form__label::before {
  content: "";
  display: inline-block;
  width: 18px;
  height: 18px;
  border-radius: 3px;
  border: 1px solid rgba(46, 50, 55, 0.3215686275);
  margin-right: 10px;
  vertical-align: middle;
  background-color: #ffffff;
}
.form__checkbox:checked + .form__label::before {
  background-image: url("../img/check.svg");
  background-repeat: no-repeat;
  background-position: center center;
}