.article {
  padding: 50px 0;
}
.article__header {
  position: relative;
  display: flex;
  gap: 32px;
  align-items: center;
  padding-bottom: 42px;
  margin-bottom: 53px;
}
.article__header::before {
  position: absolute;
  z-index: 1;
  content: "";
  bottom: 1px;
  width: 60%;
  height: 1px;
  background-color: #D8B075;
}
.article__header::after {
  position: absolute;
  z-index: 2;
  content: "";
  bottom: 0;
  width: 30%;
  height: 3px;
  background-color: #AA804F;
}
.article__header h1 {
  display: inline-block;
  width: 100%;
  margin-top: -18px;
  font-family: Optima;
  font-size: 42px;
  font-weight: 400;
  line-height: 54.6px;
  letter-spacing: -0.01em;
  text-align: left;
  color: #181C1D;
  text-transform: uppercase;
}
.article__header--inner {
  margin-bottom: 0;
}
.article__header--inner .service__header-text {
  width: 100%;
}
.article__header--inner::before {
  width: 100%;
}
.article__header--inner::after {
  width: 50%;
}
@media (max-width: 900px) {
  .article__header {
    flex-direction: column;
  }
  .article__header::before {
    width: 100%;
  }
  .article__header::after {
    left: 0;
    width: 50%;
  }
  .article__header-value {
    font-size: 150px;
    font-weight: 400;
    line-height: 190px;
  }
  .article__header-text {
    font-size: 32px;
    line-height: 45px;
    width: 100%;
    text-align: center;
  }
}
.article__wrapper {
  display: flex;
  gap: 36px;
  justify-content: space-between;
}
@media (max-width: 1200px) {
  .article__wrapper {
    flex-direction: column;
  }
}
.article .aside {
  width: 348px;
  flex-shrink: 0;
}
.article .aside__head {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
}
.article .aside__header {
  font-family: Optima;
  font-size: 24px;
  font-weight: 400;
  line-height: 26.71px;
  text-align: left;
  color: #181C1D;
}
.article .aside__link {
  font-family: Onest;
  font-size: 16px;
  font-weight: 400;
  line-height: 22.4px;
  text-align: left;
  text-decoration-line: underline;
  color: #AA804F;
  text-transform: lowercase;
}
.article .aside__column {
  display: flex;
  flex-direction: column;
  gap: 16px;
}
@media (max-width: 1200px) {
  .article .aside {
    width: 100%;
  }
  .article .aside__column {
    flex-direction: row;
    overflow-x: scroll;
  }
  .article .aside__column .catalog__item {
    flex-shrink: 0;
    width: 330px;
  }
}

article {
  max-width: 968px;
}
@media (max-width: 1200px) {
  article {
    max-width: 100%;
    width: 100%;
  }
}
article blockquote {
  position: relative;
  margin: 0;
  padding-left: 55px;
  color: #AA804F;
  margin: 52px 0;
}
article blockquote p {
  font-family: Onest;
  font-size: 18px;
  font-weight: 400;
  line-height: 25.2px;
  text-align: left;
  text-underline-position: from-font;
  text-decoration-skip-ink: none;
}
article blockquote cite {
  font-family: Onest;
  font-size: 18px;
  font-weight: 500;
  line-height: 25.2px;
  text-align: left;
  font-style: normal;
  color: rgba(24, 28, 29, 0.3215686275);
}
article blockquote::before {
  content: "";
  position: absolute;
  top: calc(50% - 17px);
  left: 0;
  height: 34px;
  width: 34px;
  background-image: url("../img/article/cite.svg");
  background-size: 32px;
  background-repeat: no-repeat;
  background-position: center center;
}
article time {
  display: inline-block;
  margin-bottom: 52px;
  margin-top: 32px;
  font-family: Onest;
  font-size: 16px;
  font-weight: 300;
  line-height: 20.4px;
  text-align: left;
  color: rgba(46, 50, 55, 0.3215686275);
}
article p {
  font-family: Onest;
  font-size: 18px;
  font-weight: 400;
  line-height: 25.2px;
  text-align: left;
  text-underline-position: from-font;
  text-decoration-skip-ink: none;
  color: rgba(24, 28, 29, 0.6392156863);
}
article h2, article h3 {
  margin-top: 52px;
  margin-bottom: 20px;
  font-family: Optima;
  font-size: 32px;
  font-weight: 400;
  line-height: 41.6px;
  letter-spacing: -0.01em;
  text-align: left;
  color: #181C1D;
}
article h3 {
  font-size: 24px;
  line-height: 31.2px;
}
article ul, article ol {
  padding-left: 1rem;
  display: flex;
  flex-direction: column;
  gap: 16px;
  margin-bottom: 52px;
  margin-top: 24px;
}
article ul li, article ol li {
  font-family: Onest;
  font-size: 18px;
  font-weight: 400;
  line-height: 25.2px;
  text-align: left;
  color: rgba(24, 28, 29, 0.6392156863);
}
article img {
  width: 100%;
  margin-top: 20px;
  margin-bottom: 52px;
  border-radius: 16px;
}